import test from 'ava'
import {pricingRules} from '../src/pricing-rules.js'

test('test pricingRules 1x ipd', t => {
  let testBasket = [{ sku: 'ipd', name: 'Super iPad', price: 549.99 }]
  let total = pricingRules(testBasket) 
  t.log(total)
  t.true(total === 549.99)
})

test('test pricingRules 3x ipd', t => {
  let testBasket = [{ sku: 'ipd', name: 'Super iPad', price: 549.99 }, 
    { sku: 'ipd', name: 'Super iPad', price: 549.99 }, 
    { sku: 'ipd', name: 'Super iPad', price: 549.99 }]
  let total = pricingRules(testBasket) 
  t.log(total)
  t.true(total === (449.99 * 3))
})